.container {
	display: flex;
}

.main-table {
	grid-template-columns: 40%, 60%;
	height: 600px;
	display: flex;
}

.left-col {
	background: linear-gradient(#0f0f0f, #08080d, #0f0f0f);
	display: flex;
	justify-content: center;
	align-items: center;
	padding: 0 10px;
}

.right-col {
	background: #08080d;
	display: flex;
	justify-content: center;
	align-items: center;
}

/***** Portfolio ******/
.wrapper {
	background-color: #4f4346;
	display: flex;
	justify-content: center;
	align-items: center;
	width: 100%;
	height: 600px;
	overflow: hidden;
}

.card {
	background-color: white;
	border-radius: 5px;
	padding: 10px;
	width: 50%;
	height: 500px;
	overflow-y: auto;
}

.p-link {
	color: blue;
	text-decoration: underline;
	margin-bottom: 3px;
}

@media (max-width: 700px) {
	.main-table {
		grid-template-columns: none;
		display: flex;
		flex-direction: column;
	}
	.card {
		width: 90%;
	}
}
